function add(pathDir) {
    // Ê£ÄÊü•Áî®Êà∑ÊòØÂê¶ÈÄâÊã©‰∫ÜÊñá‰ª∂
    if (imageInput.files.length === 0) {
        console.log('ËØ∑ÂÖàÈÄâÊã©Ë¶Å‰∏ä‰º†ÁöÑÂõæÂÉèÊñá‰ª∂'); // Â¶ÇÊûúÊ≤°ÊúâÈÄâÊã©Êñá‰ª∂ÔºåÊòæÁ§∫ÊèêÁ§∫Âπ∂ÁªàÊ≠¢Êìç‰Ωú
        return;
    }
    if(pathDir == '/') {
        console.log('ËØ∑ÂÖàÈÄâÊã©Êñá‰ª∂Â§π');
        return;
    }

    const file = imageInput.files[0]; // Ëé∑ÂèñÁî®Êà∑ÈÄâÊã©ÁöÑÁ¨¨‰∏Ä‰∏™ÂõæÂÉèÊñá‰ª∂

    const formData = new FormData(); // ÂàõÂª∫ FormData ÂØπË±°ÔºåÁî®‰∫éÊûÑÂª∫Ë¶Å‰∏ä‰º†ÁöÑÊï∞ÊçÆ
    formData.append('file', file); // Â∞ÜÈÄâÂÆöÁöÑÊñá‰ª∂Ê∑ªÂä†Âà∞ FormData ÂØπË±°‰∏≠Ôºå‰ΩøÁî® "file" ‰Ωú‰∏∫Â≠óÊÆµÂêçÁß∞

    const reader = new FileReader(); // ÂàõÂª∫ FileReader ÂØπË±°ÔºåÁî®‰∫éËØªÂèñÊñá‰ª∂ÂÜÖÂÆπ
    reader.readAsDataURL(file); // ËØªÂèñÊñá‰ª∂ÂÜÖÂÆπÂπ∂Â∞ÜÂÖ∂‰ª•DataURLÊ†ºÂºèËØªÂèñ

    reader.onload = function () {
        const content = reader.result.split(',')[1]; // Ëé∑ÂèñBase64ÁºñÁ†ÅÂêéÁöÑÊñá‰ª∂ÂÜÖÂÆπ

        // ‰ΩøÁî® fetch ÊñπÊ≥ïÂêë GitHub ÁöÑ API Á´ØÁÇπÂèëÈÄÅ PUT ËØ∑Ê±ÇÔºåÂ∞ÜÂõæÂÉèÊñá‰ª∂ÂÜÖÂÆπ‰∏ä‰º†Âà∞ÊåáÂÆöÁöÑ GitHub ‰ªìÂ∫ì
        fetch(`https://api.github.com/repos/${repoOwner}/${repoName}/contents/${pathDir}/${file.name}`, {
            method: 'PUT',
            headers: {
                'Authorization': `token ${token}`, // ‰ΩøÁî® GitHub ËÆøÈóÆ‰ª§ÁâåËøõË°åË∫´‰ªΩÈ™åËØÅ
            },
            body: JSON.stringify({
                message: 'Upload image',
                content: content, // ‰ΩøÁî®Base64ÁºñÁ†ÅÂêéÁöÑÂÜÖÂÆπ
                branch: 'master', // ÂàÜÊîØÂêçÁß∞ÔºåÈÄöÂ∏∏‰∏∫ 'main' Êàñ 'master'
            }),
        })
            .then(response => response.json()) // Ëß£ÊûêÂìçÂ∫îÁöÑ JSON Êï∞ÊçÆ
            .then(data => {
                const imageUrl = data.content.download_url; // ÊèêÂèñÂõæÂÉèÁöÑ‰∏ãËΩΩÈìæÊé•
                // ÊòæÁ§∫ÂõæÁâáÈìæÊé•ÁªôÁî®Êà∑
                alert('‰∏ä‰º†ÊàêÂäüÔºåÂõæÁâáÈìæÊé•Ôºö' + imageUrl);
                // Êõ¥Êñ∞
                getImg(curryPath);
            })
            .catch(error => {
                console.error(error);
            });
    };
}

/**
 * Âà†
 * Âà†Èô§github‰ªìÂ∫ìÁöÑÊüê‰∏™Êñá‰ª∂
 * Êñá‰ª∂Â§π‰∏≠Êñá‰ª∂Âà†ÂÆåÂ∞±Ëá™Âä®Âà†Èô§Êñá‰ª∂Â§π‰∫ÜÔºàÊöÇÊó†ÊâπÈáè
 * author:zh1
 * date:2023Âπ¥10Êúà23Êó•
 */
// ÈÄöËøá GitHub API Ëé∑ÂèñÊñá‰ª∂ÁöÑ SHA(SHA‰∏∫Âà†Èô§‰ªìÂ∫ìÊñá‰ª∂ÁöÑÂøÖÈ°ªÂèÇÊï∞)
async function getShaForFile(filePath) {
    const apiUrl = `https://api.github.com/repos/${repoOwner}/${repoName}/contents/${filePath}`;
    const headers = {
        'Authorization': `token ${token}`,
    };

    const response = await fetch(apiUrl, {
        headers,
    }).then(response => {
        if (response.ok) {
            return response.json()
        } else {
            console.error('Ëé∑ÂèñÊñá‰ª∂ SHA Â§±Ë¥•');
            return null;
        }
    }).then(data => {
        return data
    });
    return response;
}

async function del(filePath) {
    // Ëé∑ÂèñÊñá‰ª∂ÁöÑ SHA
    const sha = await getShaForFile(filePath);
    console.log("üöÄ ~ file: index.js:93 ~ del ~ sha:", sha)

    if (sha) {
        // ‰ΩøÁî® SHA Âà†Èô§Êñá‰ª∂
        const apiUrl = `https://api.github.com/repos/${repoOwner}/${repoName}/contents/${filePath}`;
        const headers = {
            'Authorization': `token ${token}`,
            'Content-Type': 'application/json',
        };

        const response = await fetch(apiUrl, {
            method: 'DELETE',
            headers,
            body: JSON.stringify({
                message: 'Delete file',
                sha: sha.sha, // Êèê‰æõ SHA
            }),
        });

        if (response.ok) {
            console.log('Êñá‰ª∂Âà†Èô§ÊàêÂäü');
        } else {
            console.error('Êñá‰ª∂Âà†Èô§Â§±Ë¥•');
        }
    } else {
        console.log('SHA‰∏çÂ≠òÂú®')
    }
}




