// Êúâ‰∏ãËΩΩÂú∞ÂùÄÔºåÈÄÇÂêàÂõæÁâáÊáíÂä†ËΩΩ
async function search() {
    const path = ''; // ËÆæÁΩÆ‰∏∫Á©∫‰ª•Ëé∑ÂèñÊï¥‰∏™‰ªìÂ∫ìÂÜÖÂÆπÔºåÊàñÊåáÂÆöË∑ØÂæÑ‰ª•Ëé∑ÂèñÁâπÂÆöÁõÆÂΩï
    // ÊûÑÂª∫ API ËØ∑Ê±ÇÁöÑ URL
    const apiUrl = `https://api.github.com/repos/${repoOwner}/${repoName}/contents/${path}`;

    // ËÆæÁΩÆËØ∑Ê±ÇÂ§¥ÔºåÂåÖÊã¨ GitHub ‰ª§Áâå
    const headers = {
        Authorization: `token ${token}`
    };

    // Ê†ëÁöÑÊñáÊ°£ÁâáÊÆµ
    const treeFragment = document.createDocumentFragment();

    // ÂèëËµ∑ GET ËØ∑Ê±ÇËé∑Âèñ‰ªìÂ∫ìÂÜÖÂÆπ
    fetch(apiUrl, { headers })
        .then(response => response.json())
        .then(data => {
            console.log(data, 'qqq')
            let element = document.createElement('div');
            element.textContent = data[0].name;
            element.className = 'imgContainer-content-left-treeNode';
            treeFragment.appendChild(element);
            // ÈÅçÂéÜÂπ∂ËæìÂá∫Êñá‰ª∂ÂíåÁõÆÂΩï
            listContents(data);
        })
        .catch(error => console.error('ÂèëÁîüÈîôËØØÔºö', error));

    // ÈÅçÂéÜÂπ∂ËæìÂá∫Êñá‰ª∂ÂíåÁõÆÂΩï
    function listContents(contents, level = 0) {
        contents.forEach(item => {
            const indent = '  '.repeat(level);
            if (item.type === 'dir') {
                // ÈÄíÂΩíËé∑ÂèñÂ≠êÁõÆÂΩïÂÜÖÂÆπ
                fetch(item.url, { headers })
                    .then(response => response.json())
                    .then(subContents => {
                        // listContents(subContents, level + 1);
                        console.log(subContents, 'sub')
                        subContents.forEach(subItem => {
                            if (subItem.type == "dir") {
                                let element = document.createElement('div');
                                element.textContent = subItem.name;
                                element.setAttribute('url', subItem.git_url);
                                element.className = 'imgContainer-content-left-treeNode';
                                treeFragment.appendChild(element);
                            }
                        })
                        treeShowEle.appendChild(treeFragment);
                    })
                    .catch(error => console.error('Ëé∑ÂèñÂ≠êÁõÆÂΩïÂÜÖÂÆπÊó∂Âá∫ÈîôÔºö', error));
            }
        });
    }
}

